@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Inter";
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: black;
}
header.header {
  background: black;
}
.header-container {
  align-items: center;
  display: flex;
}
.mobile-menu {
  display: none;
}
.logo {
  width: auto;
  height: 50px;
  margin-left: 40px;
}
.logo a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-decoration: none;
  width: 100%;
  height: 100%;
  font-size: 18px;
}
.logo a img {
  width: 37%;
  height: 70%;
}

.header-container > div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-header {
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-header ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}
.nav-header li.link-list {
  padding: 18px;
  position: relative;
}
.nav-header a.link-item {
  font-size: 18px;
  text-decoration: none;
  color: white;
}
ul.p_f_subItem {
  list-style: none;
  position: absolute;
  top: 55px;
  background: black;
  width: 210px;
  left: 28px;
  display: none;
  z-index: 5;
}
li.link-list:hover ul.p_f_subItem {
  display: block;
}
li.p-f-listItem {
  padding: 10px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
li.p-f-listItem:nth-child(2) {
  border-bottom: 1px solid #ffffff30;
}
li.p-f-listItem a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  margin-left: 10px;
  font-weight: 300;
}
li.p-f-listItem i.fas.fa-caret-right {
  position: absolute;
  right: 20px;
  color: white;
}

ul.creationSubItem {
  position: absolute;
  right: -210px;
  width: 210px;
  top: 0;
  background: black;
  list-style: none;
  display: none;
  z-index: 5;
}
li.p-f-listItem:hover ul.creationSubItem {
  display: block;
}
li.creationListItem {
  padding: 10px;
  display: flex;
  align-items: center;
}
li.creationListItem a {
  margin-left: 18px;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
}
svg.iconDesign {
  font-size: 20px;
  position: absolute;
  right: 5px;
}
a.bigConten {
  display: flex;
  align-items: center;
}
ul.l_f_subItem {
  list-style: none;
  position: absolute;
  top: 55px;
  background: black;
  width: 205px;
  display: none;
  left: 22px;
  z-index: 1;
}
.link-list:hover ul.l_f_subItem {
  display: block;
}

.l-listItem svg.iconDesign {
  font-size: 20px;
  position: absolute;
  right: 6px;
}
li.l-listItem {
  padding: 12px;
  position: relative;
  display: flex;
  align-items: center;
}
li.l-listItem a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  margin-left: 10px;
  font-weight: 300;
  position: relative;
  width: 100%;
  align-items: center;
  display: flex;
}
.search {
  width: 175px;
  justify-content: center;
  display: flex;
  position: relative;
  align-items: center;
  margin-right: 10px;
  margin-left: 10px;
  color: white;
  font-weight: 100;
}
.search input[type="text"] {
  font-size: 17px;
  width: 100%;
  border: none;
  margin-right: 5px;
  outline: none;
  padding: 5px;
  padding-left: 30px;
  background: #2d2d2d;
  color: white;
  font-weight: 100;
}
.search input::placeholder {
  color: white;
  font-family: Inter;
  font-weight: 100;
}
svg.mySearch {
  position: absolute;
  left: 6px;
  font-size: 22px;
}
.search input::placeholder {
  margin-left: 25px;
}
.icon-info {
  width: auto;
  justify-content: center;
  display: flex;
  align-items: center;
  color: white;
  column-gap: 14px;
  margin-right: 14px;
}
.box-icon-container {
  display: flex;
  align-items: center;
  position: relative;
}
.box-icon-container i.fas.fa-th.box-icon {
  font-size: 22px;
  border-radius: 50%;
  padding: 12px;
  cursor: pointer;
}
.box-icon-container i.fas.fa-th.box-icon:hover {
  background: #2b2b2b;
}
.user-icon:hover {
  background: #2b2b2b;
}
.user-icon-container {
  align-items: center;
  display: flex;
  position: relative;
}
.user-icon-container i.fas.fa-user-circle.user-icon {
  font-size: 20px;
  cursor: pointer;
}
.box-icon-container.active i {
  background: #2b2b2b;
}
.box-icon-container.active > i,
.user-icon-container.active > i {
  color: #2196f3;
}

.AppSubItem-Container {
  width: 225px;
  position: absolute;
  background: black;
  top: 53px;
  right: 4px;
  z-index: 5;
  display: none;
}
.box-icon-container.active .AppSubItem-Container {
  display: block;
}

.app-sub-item {
  margin: 20px;
  margin-bottom: 30px;
  font-size: 14px;
}
.app-sub-item a {
  color: white;
  text-decoration: none;
  position: relative;
  display: flex;
  align-items: center;
}
svg.app-little-icon {
  position: absolute;
  left: 18px;
}
svg.app-little-icon.compass {
  font-size: 20px;
}
img.app-little-icon {
  position: absolute;
  left: 15px;
}
img.app-little-icon.distribute {
  left: 18px;
}
span.app-anchor {
  margin-left: 60px;
}
.UserSubItem-Container {
  width: 225px;
  position: absolute;
  background: black;
  top: 52px;
  right: 5px;
  z-index: 1;
  display: none;
}
.user-icon-container.active .UserSubItem-Container {
  display: block;
}
.user-icon-container.active .user-icon {
  color: #2196f3;
}
.user-icon {
  font-size: 22px;
  padding: 12px;
  cursor: pointer;
  border-radius: 50%;
}
.user-sub-item {
  margin: 10px;
  margin-bottom: 20px;
  margin-top: 8px;
  font-size: 14px;
}
.user-sub-item a {
  color: white;
  text-decoration: none;
  position: relative;
  display: flex;
  align-items: center;
}
.user-little-icon {
  position: absolute;
  left: 10px;
}
span.user-anchor {
  margin-left: 55px;
}

.search {
  width: 175px;
  justify-content: center;
  display: flex;
  position: relative;
  align-items: center;
  margin-right: 10px;
  margin-left: 10px;
  color: white;
  font-weight: 100;
}
.search input[type="text"] {
  font-size: 17px;
  width: 100%;
  border: none;
  margin-right: 5px;
  outline: none;
  padding: 5px;
  padding-left: 30px;
  background: #2d2d2d;
  color: white;
  font-weight: 100;
}
.search input::placeholder {
  color: white;
  font-family: Inter;
  font-weight: 100;
}
svg.mySearch {
  position: absolute;
  left: 6px;
  font-size: 22px;
}
.search input::placeholder {
  margin-left: 25px;
}
.body {
  display: flex;
  justify-content: center;
}
.body-container {
  width: 1140px;
}
.hero-img {
  position: relative;
  overflow: hidden;
  height: 400px;
  align-items: center;
  display: flex;
  justify-content: flex-start;
}
.hero-img-container {
  height: 100%;
  position: absolute;
}
.hero-img-container img {
  width: 100%;
  height: 100%;
}
.hero-img-content {
  z-index: 1;
  color: white;
  margin-left: 40px;
}

.hero-img-container::before {
  height: 100%;
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  background-color: #0000009c;
  content: "";
}
.Sub-header {
  background: black;
  display: flex;
  flex-wrap: wrap;
}
.hero-img-content h3 {
  margin-top: 30px;
  display: flex;
  width: 75%;
  flex-wrap: wrap;
}
.Sub-header-container {
  padding: 20px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-left: 30px;
}
.Sub-anchor {
  padding: 5px;
  margin-right: 5px;
  color: white;
  text-decoration: none;
}
.Sub-anchor.active {
  border-bottom: 2px solid #6767ff;
}
.page {
  margin-left: 25px;
  margin-top: 45px;
  flex-wrap: wrap;
}
.page > h1 {
  margin-bottom: 25px;
}
.page > h4 {
  font-size: 17px;
  margin-bottom: 15px;
  margin-left: 5px;
  font-weight: 600;
  color: black;
}
/* Card Css Style */
.card {
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
}
.cards {
  display: flex;
  width: 23%;
  height: 215px;
  flex-direction: column;
  margin: 6px;
  border: 0.5px solid #969696;
  border-radius: 5px;
  box-shadow: 0px 1px 4px 0px #969696;
}
.card-header {
  display: flex;
  align-items: center;
  margin: 20px 3px;
  height: 20%;
}
.card-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  width: 40px;
}
.card-icon svg.Card_Icon {
  font-size: 22px;
  margin-left: 15px;
}
h2.card-title {
  font-size: 16px;
  width: 155px;
  font-weight: 700;
  padding-right: 10px;
}
.beta {
  margin-right: 10px;
  width: 40px;
  background: black;
  color: white;
  padding-left: 7px;
  padding-right: 7px;
  font-size: 12px;
  border-radius: 4px;
}
.card-body {
  font-size: 14px;
  line-height: 15px;
  margin-left: 18px;
  height: 30%;
  color: black;
  font-weight: 600;
}
.card-footer {
  margin-left: 10px;
  margin-top: 5px;
  padding: 8px;
}
.card-button {
  text-decoration: none;
  word-spacing: -2px;
  font-weight: 600;
  background: white;
  padding: 8px;
  padding-left: 15px;
  padding-right: 15px;
  color: #1e86e0;
  font-size: 14px;
  border-radius: 3px;
  border: 0.5px solid #1e86e0;
}
.card-button.active {
  background: #2196f3;
  color: white;
}

.footer {
  background: black;
  margin-top: 50px;
}
.footer-container {
  display: flex;
  padding: 25px;
}
.footero-logo {
  width: 40%;
  display: flex;
  justify-content: center;
}
.footer-content-holder p {
  color: white;
}
a.footer-logo-holder {
  width: 120px;
  margin-left: 150px;
}
a.footer-logo-holder img {
  width: 100%;
}
.footer-content-holder {
  width: 50%;
  padding: 5px;
  justify-content: center;
  display: flex;
  align-items: center;
}

.footer-content-holder p {
  color: white;
}
/* Responsive Start From Here */
@media (max-width: 1150px) {
  .logo {
    height: 45px;
    margin-left: 15px;
  }
  .logo a {
    font-size: 17px;
  }
  .nav-header li.link-list {
    padding: 14px;
  }
  .nav-header a.link-item {
    font-size: 15px;
  }
  ul.p_f_subItem {
    width: 190px;
    left: 24px;
  }
  ul.l_f_subItem {
    width: 195px;
    left: 17px;
  }
  .l-listItem svg.iconDesign {
    font-size: 18px;
    position: absolute;
    right: 10px;
  }
  .search {
    width: 165px;
    margin-right: -5px;
    margin-left: 0px;
  }
  .search input[type="text"] {
    font-size: 14px;
  }
  .box-icon-container i.fas.fa-th.box-icon {
    font-size: 20px;
  }
  .AppSubItem-Container {
    top: 48px;
  }
  .app-sub-item {
    font-size: 14px;
  }
  .UserSubItem-Container {
    top: 48px;
  }
  .user-sub-item {
    font-size: 14px;
  }
  .icon-info {
    column-gap: 0px;
    margin-right: 10px;
  }
}
/* Responsive 991px width */
@media (max-width: 991px) {
  .logo {
    width: auto;
    height: 38px;
    margin-left: 10px;
  }
  .logo a {
    font-size: 14px;
  }
  .nav-header ul {
    margin-right: -16px;
  }
  .nav-header li.link-list {
    padding: 6px;
  }
  li.p-f-listItem a {
    font-size: 11px;
    margin-left: 8px;
  }
  ul.p_f_subItem {
    top: 40px;
    width: 163px;
    left: 7px;
    z-index: 5;
  }
  ul.creationSubItem {
    right: -142px;
    width: 160px;
  }
  ul.l_f_subItem {
    list-style: none;
    position: absolute;
    top: 40px;
    background: black;
    width: 165px;
    display: none;
    left: 4px;
    z-index: 5;
  }
  ul.l_f_subItem.support {
    width: 220px;
  }
  .nav-header a.link-item {
    font-size: 14px;
  }
  svg.iconDesign {
    font-size: 16px;
    position: absolute;
    right: 5px;
  }
  li.l-listItem a {
    font-size: 11px;
  }
  .l-listItem svg.iconDesign {
    font-size: 16px;
    position: absolute;
    right: 6px;
  }
  li.p-f-listItem i.fas.fa-caret-right {
    font-size: 15px;
  }
  .search {
    width: 140px;
    margin-right: 8px;
    margin-left: 8px;
  }
  .search input[type="text"] {
    font-size: 14px;
    padding: 3px;
    padding-left: 32px;
  }
  svg.mySearch {
    font-size: 18px;
  }

  .icon-info {
    column-gap: 0px;
  }
  .box-icon-container i.fas.fa-th.box-icon {
    font-size: 20px;
    border-radius: 50%;
    padding: 11px;
    cursor: pointer;
  }
  .user-icon-container i.fas.fa-user-circle.user-icon {
    font-size: 19px;
    cursor: pointer;
  }

  .AppSubItem-Container {
    width: 200px;
    top: 42px;
  }
  .app-sub-item {
    margin: 6px;
  }
  .app-sub-item a {
    font-size: 12px;
  }
  img.app-little-icon {
    position: absolute;
    left: 14px;
    height: 17px;
  }
  img.app-little-icon.distribute {
    left: 16px;
  }
  .UserSubItem-Container {
    width: 210px;
    top: 43px;
    right: 5px;
  }
  .user-sub-item {
    margin-bottom: 15px;
  }
  .user-sub-item a {
    font-size: 12px;
  }
  .card {
    margin-right: 25px;
  }
  .cards {
    width: 31.5%;
  }

  .card-body {
    margin-right: 18px;
  }
}
/* Responsive 868px width */
@media only screen and (max-width: 868px) {
  .logo {
    width: auto;
    height: 34px;
    margin-left: 6px;
  }
  .logo a {
    font-size: 12px;
  }
  .nav-header {
    margin-left: 10px;
  }
  .nav-header li.link-list {
    padding: 8px;
  }
  .nav-header a.link-item {
    font-size: 12px;
  }
  .search input[type="text"] {
    font-size: 12px;
    padding: 3px;
    padding-left: 30px;
  }
  .search {
    width: 108px;
    margin-right: 6px;
    margin-left: 6px;
  }
  svg.mySearch {
    font-size: 15px;
    margin-top: 2px;
  }
  .box-icon-container i.fas.fa-th.box-icon {
    font-size: 15px;
  }
  .user-icon-container i.fas.fa-user-circle.user-icon {
    font-size: 15px;
    cursor: pointer;
  }
  .icon-info {
    column-gap: 0px;
    margin-right: 6px;
  }
  ul.p_f_subItem {
    top: 38px;
    width: 152px;
    left: 10px;
    z-index: 5;
  }
  li.p-f-listItem {
    padding: 5px;
  }
  li.p-f-listItem a {
    font-size: 11px;
  }
  li.l-listItem {
    padding: 8px;
  }
  li.l-listItem a {
    font-size: 11px;
  }
  .l-listItem svg.iconDesign {
    font-size: 15px;
  }
  ul.l_f_subItem {
    list-style: none;
    position: absolute;
    top: 42px;
    background: black;
    width: 145px;
    display: none;
    left: 4px;
    z-index: 5;
  }
  ul.l_f_subItem.support {
    width: 165px;
  }
  .AppSubItem-Container {
    width: 165px;
    top: 39px;
  }
  .app-sub-item a {
    font-size: 11px;
  }
  .UserSubItem-Container {
    width: 175px;
    top: 40px;
    right: 5px;
  }
  .user-sub-item a {
    font-size: 11px;
  }
  span.user-anchor {
    margin-left: 40px;
  }
  .card {
    margin-right: 30px;
  }
  .cards {
    display: flex;
    width: 48%;
    margin: 6px auto;
  }
}
@media only screen and (max-width: 767px) {
  .logo {
    width: auto;
    height: 34px;
    margin-left: 30px;
    display: flex;
  }
  .nav-header {
    display: none;
  }
  .icon-info {
    column-gap: 0px;
    margin-right: 30px;
  }
  .AppSubItem-Container {
    top: 38px;
  }
  .UserSubItem-Container {
    top: 39px;
  }
  svg.hamburgerMenu {
    color: white;
    cursor: pointer;
    width: 35px;
    height: 20px;
  }
  .page {
    padding-top: 40px;
    padding-left: 15px;
  }
  .cards {
    width: 80%;
    margin: 10px auto;
  }
  .card-header {
    width: 100%;
  }
  .card-icon {
    width: 15%;
  }
  h2.card-title {
    width: 70%;
  }
  .beta {
    width: 15%;
    display: flex;
    justify-content: center;
    background: black;
    color: white;
    padding-left: 0px;
    padding-right: 0px;
    font-size: 12px;
  }
  .footer-container {
    display: flex;
    flex-direction: column;
  }
  .footero-logo {
    width: 100%;
    display: flex;
    justify-content: center;
    height: 60px;
  }
  a.footer-logo-holder {
    width: auto;
    height: auto;
    margin-left: 0px;
  }
  a.footer-logo-holder img {
    width: auto;
    height: 100%;
  }
  .footer-content-holder {
    width: 100%;
    padding: 20px;
  }
}
@media only screen and (max-width: 419px) {
  .logo {
    margin-left: 5px;
    margin-right: 5px;
  }
  .logo a img {
    width: 25%;
    height: 60%;
  }
  .search {
    width: 0px;
    padding-right: 10px;
  }
  .search input[type="text"] {
    display: none;
  }
  .box-icon-container {
    margin-right: -12px;
  }
  .icon-info {
    column-gap: 0px;
    margin-right: 20px;
  }
  svg.hamburgerMenu {
    margin-left: -25px;
  }
}
@media only screen and (max-width: 270px) {
  .logo {
    margin-left: 25px;
    margin-right: 25px;
  }
  .header-container {
    width: 270px;
    display: inline-flex;
  }
}
